import React from 'react'
import { dollySlack } from '~/components/dollySlack/dollySlack'

type VarslingId = {
	varslingId: string
}

export const VarslingerTekster = ({ varslingId }: VarslingId) => {
	if (!varslingId) return null

	const brukerveiledning = (
		<a href="https://navikt.github.io/dolly-frontend/" target="_blank">
			her
		</a>
	)
	const retningslinjer = (
		<a href="https://navikt.github.io/dolly-frontend/retningslinjer" target="_blank">
			her
		</a>
	)

	switch (varslingId) {
		case 'VELKOMMEN_TIL_DOLLY':
			return (
				<>
					<h1>Velkommen til Dolly</h1>

					<h2>Prinsipper for bruk av Dolly</h2>
					<p>
						Dolly selvbetjening brukes for  lage egne testdata til randtilfeller og spesialbehov.
						Syntetiske personer gis egenskaper fra registre og fagsystemer - f.eks. sivilstand,
						inntekt, statsborgerskap. Brukere av Dolly selvbetjening har selv ansvar for  flge
						flgende prinsipper ved oppretting av syntetiske testdata:
					</p>
					<ul>
						<li>
							Dolly tilbyr et stort utvalg attributter som kan settes p en person. Selv om det kun
							er syntetiske personer som blir laget i Dolly, finnes det allikevel en risiko for 
							skape gjenkjennbare personer, ved  kombinere verdier som er svrt spesifikke for en
							ekte person. Derfor m du aldri ta utgangspunkt i reelle personer nr du oppretter
							syntetiske testpersoner - alle verdier som settes for  dekke behovet m vre
							tilfeldig valgt.
						</li>
						<li>
							Nr du er logget inn i Dolly har du tilgang til alle brukeres testdatagrupper og
							testpersoner. Ikke gjr endringer p eller slett andres testdatagrupper eller
							testpersoner uten at dette er avtalt med eier.
						</li>
						<li>
							Dolly selvbetjening har personlig innlogging med Azure AD. Ikke del
							ploggingsinformasjonen din med andre brukere. Brukere som mangler tilgang til Dolly
							bes kontakte teamet for  f hjelp til  ordne dette.
						</li>
						<li>
							Snakk med oss! Skulle det vre noe, vil vi gjerne at du kontakter oss p #dolly p
							Slack. Innspill, nsker, meldinger om feil, osv. hjelper oss med  gjre Dolly bedre,
							og kommer alle brukerne til gode.
						</li>
					</ul>
					<p>
						Du kan lese mer om retningslinjer for bruk av testdata i NAV {retningslinjer}, eller
						under Dokumentasjon i menyen.
					</p>

					<h2>Hjelp til  bruke Dolly</h2>
					<p>
						Om dette er frste gang du bruker Dolly, vil du sikkert ta en titt p brukerveiledningen
						vr. Den finner du {brukerveiledning}, eller under Dokumentasjon i menyen.
					</p>
					<p>
						Kontakt oss gjerne p {dollySlack} dersom det er noe du lurer p eller trenger hjelp
						med.
					</p>
				</>
			)
		case 'AZURE_AD_INNLOGGING':
			return (
				<>
					<h1>Ny innlogging i Dolly</h1>

					<h2>Z-bruker er erstattet med personlig innlogging</h2>
					<p>
						Som du sikkert la merke til da du logget deg inn i Dolly, er den gamle innloggingen med
						Z-bruker fjernet til fordel for personlig innlogging med AzureAD. Dette er gjort for 
						gjre det lettere  ta i bruk Dolly, gi deg bedre oversikt over testdataene dine, og
						lettere kunne hjelpe deg dersom det skulle oppst en feil.
					</p>

					<h2>Hvor er testpersonene dine?</h2>
					<p>
						Ikke bekymre deg - testpersonene dine er ikke slettet! Men fordi de er koblet til
						Z-brukeren din m du importere dem til din personlige brukerkonto for  f tilgang til
						dem. Dette kan du gjre frste gang i testdatagruppe-oversikten, eller nr som helst p
						Min side.
					</p>
				</>
			)
		default:
			return null
	}
}
