import React from 'react'
import { ErrorBoundary } from '~/components/ui/appError/ErrorBoundary'

export const CriticalError = (props: { error: string }) => {
	const generateCriticalErrorMessage = (stackTrace: string) => {
		if (stackTrace.includes('miljoer'))
			return 'Problemer med  hente gyldige miljer. Prv  refresh siden (ctrl + R).'
		else if (stackTrace.includes('current'))
			return 'Problemer med  hente Azure id for innlogget bruker. Prv  refresh siden (ctrl + R).'
		else return 'Problemer med  hente dolly config. Prv  refresh siden (ctrl + R).'
	}

	return (
		<ErrorBoundary
			error={generateCriticalErrorMessage(props.error)}
			stackTrace={props.error}
			style={{ margin: '25px auto' }}
		/>
	)
}
